:root{
    --main-color: #f1f3f6;
    --shadow-color: rgba(55, 84, 170, 0.15);
}
body{
    background: var(--main-color);
}

textarea:focus, input:focus{
    outline: none;
}

.main-background{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    background-color: #f1f3f6;
}

.datum-header{
    text-align: center;
    text-align: center;
    font-size: 14px;
    font-weight: lighter;
    color: darkgray;
    font-family: Quicksand, sans-serif;
}

.headline{
    text-align: center;
    margin-top: 0px;
    margin-bottom: 40px;
    font-family: Quicksand, sans-serif;
    font-size: 25px;
    font-weight: lighter;
    color: rgb(85, 93, 113);
}

.today{
    display: none;
    cursor: pointer;
    font-size: 14px;
    font-family: Quicksand,sans-serif;
    color: orange;
    position: absolute;
    top: 50px;
}

.today1{
    cursor: pointer;
    font-size: 14px;
    font-family: Quicksand,sans-serif;
    color: orange;
    position: absolute;
    top: 50px;
}

.btn-oben{
    margin-bottom: 50px;
    display:flex;
    justify-content: center;
    align-items:center;
    width: 200px;
    height: 200px;
    background: var(--main-color);
    border-radius: 40px;
    box-shadow:
            inset 0 0 15px rgba(55, 84, 170, 0),
            inset 0 0 20px rgba(255, 255, 255, 0),
            10px 10px 18px var(--shadow-color),
            -10px -10px 22px #fff,
            inset 0px 0px 4px rgba(255, 255, 255, 0.2);
    transition: 0.4s cubic-bezier(0,.57,.49,1.51);
    top:0;left:0;right:0;bottom:0;
    cursor: pointer;
}

.btn-oben-open{
    position: relative;
    margin-bottom: 50px;
    display:flex;
    justify-content: center;
    align-items:center;
    width: 500px;
    height: 200px;
    background: var(--main-color);
    border-radius: 40px;
    box-shadow:
            inset 0 0 15px rgba(55, 84, 170, 0),
            inset 0 0 20px rgba(255, 255, 255, 0),
            10px 10px 18px var(--shadow-color),
            -10px -10px 22px #fff,
