    color: rgb(85, 93, 113);
    border: none;
    padding-left: 20px;
    width: 300px;
    height: 140px;
    background: var(--main-color);
    border-radius: 40px;
    box-shadow:
            inset 7px 7px 15px var(--shadow-color),
            inset -7px -7px 20px #fff,
            0px 0px 4px rgba(255, 255, 255, 0.2),
            5px 5px 22px #fff;
    transform: scale(0.9);
